@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/AmountFormatter";
@use "@app/bancor-components/Element/DateTime";
@use "@app/bancor-components/Element/Form/Input";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Shadow";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/screen";

$action-buttons-width: 3.1875rem;
$input-edit-alias-width: 20.6rem;

.holdingDetail {
    @include Margin.set($left: beta, $right: beta, $top: gamma, $bottom: beta);
    @include Padding.set($bottom: iosBottom);

    @include screen.xl {
        grid-column: 2;
        @include Margin.set($top: delta, $left: delta, $right: delta);
    }

    #{Text.$selector}.title {
        @include Font.set($size: kappa, $weight: bold);
        @include Margin.set($bottom: delta);
    }

    .edit {
        @include Margin.set($bottom: beta);
        @include Display.set($display: grid);
        @include Shadow.set($box: alpha);
        @include Border.set($radius: beta);

        @include screen.xl {
            grid-template-columns: 2fr 1fr;
            grid-gap: Layout.get-space(delta);
            @include Shadow.set($style: none);
            @include Border.set($style: none);
        }

        > div {
            @include Flex.set($dir: column, $align: center, $justify: center);
            @include Padding.set($preset: beta);
            @include Color.set($bg: white);

            &.editActions {
                @include Flex.set($dir: default);
            }

            @include screen.xl {
                @include Shadow.set($box: alpha);
                @include Border.set($radius: beta);
            }

            #{Text.$selector}.editTitle {
                @include Color.set($fg: black);
            }

            #{AmountFormatter.$selector} {
                > span,
                > data {
                    @include Color.set($fg: "black");
                    @include Font.set($size: iota, $weight: bold, $family: heading);
                }
            }

            #{Button.$selector} {
                width: $action-buttons-width;
                @include Border.set($style: default);
                @include Padding.set($preset: none);
                @include Margin.set($left: alpha, $right: alpha);
                @include Color.set($bg: transparent);
                @include Flex.set($dir: column, $align: center);
                cursor: pointer;

                > span {
                    @include Font.set($align: center);
                }
            }
        }
    }

    .info {
        @include screen.xl {
            @include Display.set($display: grid);
            grid-template-columns: 1fr 1fr;
            grid-gap: Layout.get-space(delta);
        }

        > div {
            @include Padding.set($preset: beta);
            @include Shadow.set($box: alpha);
            @include Border.set($radius: beta);
            @include Margin.set($bottom: beta);
            @include Color.set($bg: white);

            > div {
                @include Flex.set($justify: between);

                &:not(:last-child) {
                    @include Margin.set($bottom: alpha);
                }
            }
            &.infoRenewal {
                @include Flex.set($justify: between);

                > div {
                    @include Flex.set($dir: column, $justify: center);

                    .infoTotalRenewal {
                        @include Color.set($fg: "black");
                    }
                }
            }

            #{AmountFormatter.$selector} {
                > span,
                > data {
                    @include Color.set($fg: "black");
                    @include Font.set($size: delta, $weight: bold);
                }
            }

            #{DateTime.$selector} {
                @include Color.set($fg: "black");
                @include Font.set($size: delta, $weight: bold);
            }
        }
    }

    .infoDeposit {
        @include Padding.set($preset: beta);
        @include Shadow.set($box: alpha);
        @include Border.set($radius: beta);
        @include Color.set($bg: white);

        > div {
            @include Flex.set($justify: between);

            #{Text.$selector}:nth-child(even),
            #{DateTime.$selector} {
                @include Color.set($fg: "black");
                @include Font.set($overflow: ellipsis, $space: nowrap);
                overflow: hidden;
            }

            &:not(:last-child) {
                @include Margin.set($bottom: alpha);
            }
        }
    }

    .shareDownload {
        @include Margin.set($top: delta);

        #{Button.$selector} {
            @include Border.set($style: default);
            @include Padding.set($preset: none);
            @include Color.set($bg: transparent);
            @include Flex.set($align: center);
            @include Margin.set($left: auto, $right: auto);
            cursor: pointer;

            > span {
                @include Margin.set($left: alpha);
            }

            &.shareButton {
                @include Flex.set($dir: column);
            }
        }
    }

    .editModal {
        #{Input.$selector} {
            @include Margin.set($left: auto, $right: auto);
        }

        &.inlineInput {
            #{Input.$selector} {
                max-width: $input-edit-alias-width;
            }
        }

        #{Text.$selector}.editBottomSheetTitle {
            @include Margin.set($bottom: beta);
            @include Color.set($fg: "gray-light");
            @include Font.set($size: gamma, $weight: default);
        }

        #{Text.$selector}.description {
            @include Color.set($fg: black);
        }

        .editModalButtons {
            width: Layout.get-max-width(alpha);
            @include Font.set($align: end);
        }

        .editBottomSheetButton {
            @include Font.set($align: center);

            #{Button.$selector} {
                @include Margin.set($bottom: delta, $top: beta);
            }
        }
    }
}
